import Swal from 'sweetalert2'

import { lang } from '../../assets/lang'
import { ecommerceHtml } from './ecommerceHtml'
import {
  handleDataOfDelay,
  handleOldDataOfDelay,
  sweetAlertCommonParams,
} from '../../utils/functions'

export const ecommerceModal = async (oldData, node) => {
  const commonParams = sweetAlertCommonParams(node)
  
  return await Swal.fire({
    ...commonParams,
    title: lang.ecommerce_modal_title,
    html: ecommerceHtml,

    willOpen: () => {
      $('.select2').select2({ 
        width: '100%',
        dropdownParent: $('#swal2-content')
      })


      getCustomFieldVariableViaAjax(function(response) {
        const optionsArray = response.custom_field_variable_dropdown_array || []
        const html = prepareDropdownLists(optionsArray)
        $('.text-text-custom-variable .dropdown-menu').html(html)  
      })

      $('.lead_first_name').on('click', function() {
        var textAreaTxt = $('.emojionearea-editor').html();

        var lastIndex = textAreaTxt.lastIndexOf("<br>");   
        var lastTag = textAreaTxt.substr(textAreaTxt.length - 4); 
        lastTag=lastTag.trim(lastTag);

        if(lastTag=="<br>")
          textAreaTxt = textAreaTxt.substring(0, lastIndex); 

        var txtToAdd = " #LEAD_USER_FIRST_NAME# ";
        var new_text = textAreaTxt + txtToAdd;
        $('.emojionearea-editor').html(new_text);
        $('.emojionearea-editor').trigger('blur');
      });

      $('.lead_last_name').on('click', function() {
        var textAreaTxt = $('.emojionearea-editor').html();
        
        var lastIndex = textAreaTxt.lastIndexOf("<br>");   
        var lastTag = textAreaTxt.substr(textAreaTxt.length - 4); 
        lastTag=lastTag.trim(lastTag);

        if(lastTag=="<br>")
          textAreaTxt = textAreaTxt.substring(0, lastIndex); 

        var txtToAdd = " #LEAD_USER_LAST_NAME# ";
        var new_text = textAreaTxt + txtToAdd;
        $('.emojionearea-editor').html(new_text);
        $('.emojionearea-editor').trigger('blur');
      });

      $('.dropdown-menu').on('click', '.dropdown-item', function(event) {
        const selectedText = event.target.innerText

        var textAreaTxt = $('.emojionearea-editor').html()
        
        var lastIndex = textAreaTxt.lastIndexOf("<br>");   
        var lastTag = textAreaTxt.substr(textAreaTxt.length - 4);
        lastTag=lastTag.trim(lastTag)

        if(lastTag=="<br>")
          textAreaTxt = textAreaTxt.substring(0, lastIndex)
        
        var txtToAdd = " #" + selectedText + "# "
        var new_text = textAreaTxt + txtToAdd;
        $('.emojionearea-editor').html(new_text);
        $('.emojionearea-editor').trigger('blur');
      });

      $('#text-text-message').html(oldData.ecommerceTextValue || '')

      $("#text-text-message").emojioneArea({
        autocomplete: false,
        pickerPosition: "bottom"
      })

      /** Ecommerce code starts here */
      var telegram_bot_id = window.xitFlowBuilderData.telegram_bot_id
      // var base_url = window.xitFlowBuilderData.base_url
      $.ajax({
        type: 'POST',
        url: window.xitFlowBuilderData.routes.flow_builder_get_store_list,
        dataType: 'JSON',
        data: { telegram_bot_id },
        beforeSend: function (xhr) {
          xhr.setRequestHeader('X-CSRF-TOKEN', window.xitFlowBuilderData.csrf_token);
        },
        success:function(response){
          $("#ecommerce-ecommerce-store").html(response.store_list)
          $('#ecommerce-ecommerce-store').val(oldData.ecommerceStoreValue || '').trigger('change')
        }
      })

      $("#ecommerce-ecommerce-store").on('change', function(e) {
        const selectedStoreId = e.target.value
        if(selectedStoreId)
        {
          $.ajax({
            type: 'POST',
            url: window.xitFlowBuilderData.routes.flow_builder_get_storewise_products,
            dataType: 'JSON',
            data: { selectedStoreId },
            beforeSend: function (xhr) {
              xhr.setRequestHeader('X-CSRF-TOKEN', window.xitFlowBuilderData.csrf_token);
            },
            success:function(response){
              $("#ecommerce-ecommerce-product").html(response.product_list)
              $('#ecommerce-ecommerce-product').val(oldData.ecommerceProductValue || '').trigger('change')
            }
          })
        }
      })

      // Sets old data
      $('#ecommerce-ecommerce-button-text').val(oldData.ecommerceButtonTextValue || '')

      // Handles old data of delay system
      handleOldDataOfDelay(oldData)
    },

    preConfirm: () => {
      const ecommerceStoreValue = $('#ecommerce-ecommerce-store').val()
      const ecommerceStoreText = $('#ecommerce-ecommerce-store option:selected').text()

      const ecommerceProductValue = $('#ecommerce-ecommerce-product').val()
      
      let ecommerceProductText = []
      $('#ecommerce-ecommerce-product option:selected').map((key, val) => {
        ecommerceProductText.push(val.text)
      })

      const ecommerceButtonTextValue = $('#ecommerce-ecommerce-button-text').val()



      if (! ecommerceStoreValue) {
        Swal.showValidationMessage(lang.ecommerce_modal_msg_store)
      } 
      // else if (! ecommerceProductValue) {
      //   Swal.showValidationMessage(lang.ecommerce_modal_msg_product)
      // }
      else if (! ecommerceButtonTextValue) {
        Swal.showValidationMessage(lang.ecommerce_modal_msg_button_name)
      }

      const ecommerceTextValue = $('#text-text-message').val()

      if (! ecommerceTextValue) {
        Swal.showValidationMessage(lang.text_modal_msg_reply_message)
      }

      // Handles data of delay system
      const delayData = handleDataOfDelay()

      return {
        ecommerceStoreValue,
        ecommerceStoreText,
        ecommerceProductValue,
        ecommerceProductText,
        ecommerceButtonTextValue,
        ecommerceTextValue,
        ...delayData,
      }
    },
  })
}

const getCustomFieldVariableViaAjax = (callback) => {
  // const base_url = window.xitFlowBuilderData.base_url
  // const instagram_bot_addon = window.xitFlowBuilderData.instagram_addon
  // const media_type = instagram_bot_addon ? 'ig' : 'fb'

  $.ajax({
    method: 'POST',
    dataType: 'JSON',
    url: window.xitFlowBuilderData.routes.flow_builder_get_custom_field_variable_dropdown,
    beforeSend: function (xhr) {
      xhr.setRequestHeader('X-CSRF-TOKEN', window.xitFlowBuilderData.csrf_token);
    },
    success: callback
  })
}

const prepareDropdownLists = (response) => {
  let html = '';

  if (Array.isArray(response)) {
    response.forEach((val, idx) => { // eslint-disable-line
      html += '<span class="dropdown-item">' + val.value + '</span>'
    })
  }

  return html;
}