<template>
  <div 
    class="xit-fb-template node" 
    @dblclick.stop="handleClick(node)"
    :class="[camelCase(node.name), hasData(node), isSelected(node)]" 
  >
    <div class="title">
      <p class="title-content"><i class="fas fa-bullhorn"></i> {{node.lingualName}}</p>
    </div>

    <div class="card-body-handler card_data_body p-0">
      <div class="card-data">
        <p v-if="node.data.campaign_name" class="broadcasting_campaign_name">
          {{ node.data.campaign_name }}
        </p>

        <p class="mb-0" v-if="Array.isArray(node.data.telegram_bot_label_ids) && (node.data.telegram_bot_label_ids.length > 0)">
          {{ included_labels }}: {{ getSelectedLabelTexts(node.data.telegram_bot_label_ids_names) }}
        </p> 

        <p class="mb-0" v-if="Array.isArray(node.data.excluded_telegram_bot_label_ids) && (node.data.excluded_telegram_bot_label_ids.length > 0)">
          {{ excluded_labels }}: {{ getSelectedLabelTexts(node.data.excluded_telegram_bot_label_ids_names) }}
        </p> 

        <p class="mb-0" v-if="node.data.schedule_type">
          {{ schedule_type }}: {{ ('later' === schedule_type) ? schedule_type_later : schedule_type_now }}
        </p> 

        <p class="mb-0" v-if="node.data.schedule_time">
          {{ schedule_time }}: {{ node.data.schedule_time }}
        </p> 

        <p class="mb-0" v-if="node.data.timezone">
          {{ timezone }}: {{ node.data.timezone }}
        </p> 
      </div>

      <div class="card-sockets">
        <div class="output_content">
          <div class="output text-right" v-for="output in outputs()" :key="output.key">
            <div class="output-title">{{output.name}}</div>
            <Socket v-socket:output="output" type="output" :socket="output.socket"></Socket>
          </div>
        </div>
        <div class="input_content">
          <div class="input text-left" v-for="input in inputs()" :key="input.key">
            <Socket v-socket:input="input" type="input" :socket="input.socket"></Socket>
            <div class="input-title">{{input.name}}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import VueRenderPlugin from 'rete-vue-render-plugin'

import { lang } from '../../assets/lang'
import { templateComponentMethods } from '../../utils/functions'

export default {
  name: 'Broadcasting',
  mixins: [VueRenderPlugin.mixin],
  components: {
    Socket: VueRenderPlugin.Socket
  },

  data: () => ({
    baseUrl: window.xitFlowBuilderData.base_url,
    
    included_labels: lang.broadcasting_template_labels_included,
    excluded_labels: lang.broadcasting_template_labels_excluded,
    schedule_type: lang.broadcasting_template_schedule_type,
    schedule_type_now: lang.broadcasting_template_schedule_type_now,
    schedule_type_later: lang.broadcasting_template_schedule_type_later,
    schedule_time: lang.broadcasting_template_schedule_time,
    timezone: lang.broadcasting_template_timezone,
  }),

  methods: {
    ...templateComponentMethods,
  }
}
</script>

<style scoped>
.card-audio-icon {font-size: 13px; color: #007bff;}
.card-data-media-audio {border-radius: 7px;}
.typing_delay {background-color:#f6f4f4; border-radius: 7px;}
.typing_display_none {text-decoration: line-through; text-decoration-color: red;}
.input_content { width:50% !important;margin-top: 25px;}
.output_content{ width:50% !important;float:right !important;}
.card-sockets { padding: 5px 0 !important;}
.card-data-media { font-size: 14px; }
.broadcasting_campaign_name {
  background-color: #f6f4f4 !important;
  border-radius: 7px;
  padding: 0px 10px;
  text-transform:capitalize;
  text-align: center;
}
</style>