import Swal from 'sweetalert2'
import { lang } from '../../assets/lang'
import { actionButtonHtml } from './actionButtonHtml'

import {
  makeAjaxCall,
  sweetAlertCommonParams,
  selectedOptionsTextArray,
  createSelect2DynamicOption,
  getSelectOptionsFromArrayOfObjects,
} from '../../utils/functions'

let isFilesAddedToHead = false
var telegram_bot_id = window.xitFlowBuilderData.telegram_bot_id
var builder_table_id = window.xitFlowBuilderData.builder_table_id

// eslint-disable-next-line
export const actionButtonModal = async (oldData, node, defaultData) => {
  const commonParams = sweetAlertCommonParams(node, '22rem')
  
  return await Swal.fire({
    ...commonParams,
    title: lang.action_button_modal_title,
    html: actionButtonHtml(oldData, node, defaultData),

    willOpen: () => {
      $('.select2').select2({ 
        width: '100%',
        dropdownParent: $('#swal2-content')
      })

      if (false === isFilesAddedToHead) {
        $('head').append('<script src="https://cdn.jsdelivr.net/npm/bootstrap-colorpicker@3.4.0/dist/js/bootstrap-colorpicker.min.js"></script>')
        $('head').append('<link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/bootstrap-colorpicker@3.4.0/dist/css/bootstrap-colorpicker.min.css">')

        $('head').append('<script src="https://cdnjs.cloudflare.com/ajax/libs/jquery-datetimepicker/2.4.3/jquery.datetimepicker.min.js"></script>')
        $('head').append('<link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/jquery-datetimepicker/2.4.3/jquery.datetimepicker.min.css">')

        isFilesAddedToHead = true
      }

      /** Handles action buttons list section */
      const actionButtonElement = $('#action-button-actions')
      const messengerEngagementPluginsGroupElement = $('#action-button-messenger-engagement-plugins-group')
      const broadcastingGroupElement = $('#action-button-broadcasting-group')

      const campaignNameElement = $('#campaign-name')
      const labelIdsElement = $("#label-ids")
      const includeLabelIdsElement = $('#include-label-ids')
      const excludeLabelIdsElement = $('#exclude-label-ids')

      createSelect2DynamicOption(labelIdsElement)

      const sendingTimeElement = $('#sending-time')
      const scheduleTimeGroupElement = $('#schedule-time-group')

      const scheduleTimeElement = $('#schedule-time')
      const timezoneElement = $('#timezone')

      // Populate old values
      $('#chatbox-bg-color').val(oldData.chatboxBgColor || '#0D8BF1')
      $('#chatbox-text-color').val(oldData.chatboxTextColor || '#FFFFFF')
      $('#chatbox-display-name').val(oldData.chatboxDisplayName || lang.action_button_html_chatbox_display_name_placeholder_telegram)
      $('#chatbox-welcome-message').val(oldData.chatboxWelcomeMessage || lang.action_button_html_chatbox_welcome_message_placeholder)
      $('input[name="chatbox-position"][value="' + oldData.chatboxPosition + '"]').prop('checked', true)
      $('input[name="chatbox-visibility"][value="' + oldData.chatboxVisibility + '"]').prop('checked', true)
      $('#chatbox-position-offset-x').val(oldData.xOffset || '10')
      $('#chatbox-position-offset-y').val(oldData.yOffset || '10')

      $('#button-text').val(oldData.buttonText || lang.action_button_html_button_text_placeholder)
      $('#button-bg-color').val(oldData.buttonBgColor || '#0D8BF1')
      $('#button-text-color').val(oldData.buttonTextColor || '#ffffff')
      $('#button-hover-bg-color').val(oldData.buttonHoverBgColor || '#367fa9')
      $('#button-hover-text-color').val(oldData.buttonHoverTextColor || '#fffddd')
      $('input[name="button-size"][value="' + oldData.buttonSize + '"]').prop('checked', true)
      $('#reference').val(oldData.reference || '')

      campaignNameElement.val(oldData.campaign_name || '')
      includeLabelIdsElement.val(oldData.telegram_bot_label_ids || '').trigger('change')
      excludeLabelIdsElement.val(oldData.excluded_telegram_bot_label_ids || '').trigger('change')

      scheduleTimeElement.val(oldData.schedule_time || '')
      timezoneElement.val(oldData.timezone || '').trigger('change')

      const oldSelectedActionButton = oldData.actionType 
        || ((defaultData && defaultData.selectedActionButton) 
          ? defaultData.selectedActionButton
          : '')

      actionButtonElement.val(oldSelectedActionButton || '').trigger('change')
      sendingTimeElement.prop('checked', ('later' === oldData.schedule_type) ? true : false)

      const labelIdsData = {
        url: window.xitFlowBuilderData.routes.flow_builder_get_label_dropdown,
        data: { 
          telegram_bot_id,
        }
      }

      function toggleActionButtonsList(selectedActionButton, oldData) {
        if (('messenger_engagement_plugin' === selectedActionButton) 
          || 'broadcasting' === selectedActionButton
        ) {
          makeAjaxCall(function(response) {
            const labelOptions = getSelectOptionsFromArrayOfObjects(response)

            labelIdsElement.html(labelOptions)
            labelIdsElement.val(oldData.labelIds || '').trigger('change')

            includeLabelIdsElement.html(labelOptions)
            excludeLabelIdsElement.html(labelOptions)

            includeLabelIdsElement.val(oldData.telegram_bot_label_ids || '').trigger('change')
            excludeLabelIdsElement.val(oldData.excluded_telegram_bot_label_ids || '').trigger('change')
          }, labelIdsData)
        }

        if ('messenger_engagement_plugin' === selectedActionButton) {
          broadcastingGroupElement.removeClass('d-block').addClass('d-none')
          messengerEngagementPluginsGroupElement.removeClass('d-none').addClass('d-block')
        } else if ('broadcasting' === selectedActionButton) {
          messengerEngagementPluginsGroupElement.removeClass('d-block').addClass('d-none')
          broadcastingGroupElement.removeClass('d-none').addClass('d-block')
        } else {
          messengerEngagementPluginsGroupElement
            .add(broadcastingGroupElement)
            .removeClass('d-block').addClass('d-none')
        }
      }

      let includedLabels = oldData.telegram_bot_label_ids ? oldData.telegram_bot_label_ids : [];
      let excludedLabels = oldData.excluded_telegram_bot_label_ids ? oldData.excluded_telegram_bot_label_ids : [];

      function onChangeIncludeLabels() {
        const countBotSubscriberData = {
          url: window.xitFlowBuilderData.routes.count_bot_subscriber_based_on_labels,
          data: { 
            telegram_bot_id,
            included_labels: includedLabels,
            excluded_labels: excludedLabels,
          }
        }

        makeAjaxCall(function(response) {
          if (Array.isArray(response)) {
            const subscribers = response[0].subscribers;
            window.xitFlowBuilderGlobalValue_BotSubscibers = subscribers;
            $('.targeted-subscribers').html(subscribers)
          }
        }, countBotSubscriberData)
      }

      function toggleSendLaterOptions(isSendingTimeChecked) {
        if (isSendingTimeChecked) {
          scheduleTimeGroupElement.removeClass('d-none').addClass('d-block')
        } else {
          scheduleTimeGroupElement.removeClass('d-block').addClass('d-none')
        }
      }

      const selectedActionButton = actionButtonElement.val()
      toggleActionButtonsList(selectedActionButton, oldData)

      actionButtonElement.on('change', function() {      
        toggleActionButtonsList(this.value, oldData)
      })

      onChangeIncludeLabels()

      includeLabelIdsElement.on('change', function() {
        includedLabels = $(this).val();
        onChangeIncludeLabels()
      })

      excludeLabelIdsElement.on('change', function() {
        excludedLabels = $(this).val();
        onChangeIncludeLabels()
      })

      const isSendingTimeChecked = sendingTimeElement.prop('checked')
      toggleSendLaterOptions(isSendingTimeChecked)
      
      sendingTimeElement.on('change', function() {
        toggleSendLaterOptions(this.checked)
      })      

      // Handles old data of Delay
      handleOldDataOfDelay(oldData)

      $(document).ready(function() {
        callDatetimePickerWhenItIsAvailable()
        callColorPickerWhenItIsAvailable()

        $('[data-toggle="tooltip"]').tooltip()
      })
    },

    preConfirm: async () => {
      const actionType = $('#action-button-actions').val()
      const actionTypeText = $('#action-button-actions option:selected').text()

      const chatboxBgColor = $('#chatbox-bg-color').val()
      const chatboxTextColor = $('#chatbox-text-color').val()
      const chatboxDisplayName = $('#chatbox-display-name').val()
      const chatboxWelcomeMessage = $('#chatbox-welcome-message').val()
      const chatboxPosition = $('input[name="chatbox-position"]:checked').val()
      const xOffset = $('#chatbox-position-offset-x').val()
      const yOffset = $('#chatbox-position-offset-y').val()

      const buttonText = $('#button-text').val()
      const buttonBgColor = $('#button-bg-color').val()
      const buttonTextColor = $('#button-text-color').val()
      const buttonHoverBgColor = $('#button-hover-bg-color').val()
      const buttonHoverTextColor = $('#button-hover-text-color').val()
      const buttonSize = $('input[name="button-size"]:checked').val()
      const chatboxVisibility = $('input[name="chatbox-visibility"]:checked').val()

      const reference = $('#reference').val()

      const labelIds = $('#label-ids').val()
      const labelIdsTexts = $('#label-ids option:selected')
      const labelIdTextsArray = selectedOptionsTextArray(labelIdsTexts)

      const campaign_name = $('#campaign-name').val()

      const telegram_bot_label_ids = $('#include-label-ids').val()
      const includeLabelIdsText = $('#include-label-ids option:selected')
      const telegram_bot_label_ids_names = selectedOptionsTextArray(includeLabelIdsText)

      const excluded_telegram_bot_label_ids = $('#exclude-label-ids').val()
      const excludeLabelIdsText = $('#exclude-label-ids option:selected')
      const excluded_telegram_bot_label_ids_names = selectedOptionsTextArray(excludeLabelIdsText)

      const schedule_type = $('#sending-time').prop('checked')
      const schedule_time = $('#schedule-time').val()
      const	timezone = $('#timezone').val()

      if (! actionType) {
        Swal.showValidationMessage(lang.action_button_modal_msg_action_type)
        return
      }

      if ('messenger_engagement_plugin' === actionType) {

        if (! chatboxBgColor || ! chatboxPosition) {
          Swal.showValidationMessage(lang.action_button_modal_msg_chatbox_attributes)
          return
        }

        if (! buttonText) {
          Swal.showValidationMessage(lang.action_button_modal_msg_button_text)
          return
        }
  
        if (! buttonBgColor 
          || ! buttonTextColor 
          || ! buttonHoverBgColor 
          || ! buttonHoverTextColor
        ) {
          Swal.showValidationMessage(lang.action_button_modal_msg_button_attributes)
          return
        }

        if (! buttonSize) {
          Swal.showValidationMessage(lang.action_button_modal_msg_button_size)
          return
        }
        
        if (! reference) {
          Swal.showValidationMessage(lang.action_button_modal_msg_reference)
          return
        }

        const regex = /[^a-zA-Z0-9]/
        
        if (regex.test(reference)) {
          Swal.showValidationMessage(lang.action_button_modal_msg_filter_reference_name)
          return
        }

        const result = await fetch(window.xitFlowBuilderData.routes.flow_builder_check_reference_field_value_exists + '/' + builder_table_id + '/' + reference)
          .then(function(response) {
            return response.json()
          })

        if (false === result.status) {
          Swal.showValidationMessage(result.message)
          return
        }
      }

      if ('broadcasting' === actionType) {
        if (! campaign_name) {
          Swal.showValidationMessage(lang.broadcasting_msg_campaign_name)
          return
        }
  
        if (schedule_type) {
          if (! schedule_time) {
            Swal.showValidationMessage(lang.broadcasting_msg_schedule_time)
            return
          }
  
          if (! timezone) {
            Swal.showValidationMessage(lang.broadcasting_msg_timezone)
            return
          }
        }

        if (window.xitFlowBuilderGlobalValue_BotSubscibers < 1) {
          Swal.showValidationMessage(lang.broadcasting_msg_targeted_subscribers)
          return
        }
      }

      const delayData = handleDataOfDelay()

      return {
        actionType,
        actionTypeText,
        
        chatboxBgColor,
        chatboxTextColor,
        chatboxDisplayName,
        chatboxWelcomeMessage,
        chatboxPosition,
        chatboxVisibility,
        xOffset,
        yOffset,
        ...delayData,

        buttonText,
        buttonBgColor,
        buttonTextColor,
        buttonHoverBgColor,
        buttonHoverTextColor,
        buttonSize,
        reference,


        
        labelIds,
        labelIdTextsArray,

        campaign_name,

        telegram_bot_label_ids,
        telegram_bot_label_ids_names,

        excluded_telegram_bot_label_ids,
        excluded_telegram_bot_label_ids_names,

        schedule_type: (true === schedule_type) ? 'later' : 'now',
        schedule_time,
        timezone,
      }
    },    
  })
}

const handleOldDataOfDelay = (oldData) => {
  const initDelayElement = $('#init-delay')
  const delayTextElement = $('#delay-delay-text')

  const initDelay = parseInt(oldData.initDelay, 10) || 0

  initDelayElement.val(initDelay)
  delayTextElement.text(initDelay + ' ' + lang.delay_html_info_sec)

  initDelayElement.on('change', function(e) {
    const currentValue = e.target.value
    delayTextElement.text(currentValue + ' ' + lang.delay_html_info_sec)
  })
}

const handleDataOfDelay = () => {
  const initDelay = $('#init-delay').val()

  if (parseInt(initDelay, 10) < 0 || parseInt(initDelay, 10) > 60) {
    return Swal.showValidationMessage(lang.delay_html_msg_delay_range)
  }

  return {
    initDelay
  }
}

const callDatetimePickerWhenItIsAvailable = () => {
  let limit = 0
  let hasScriptAvailable = false

  const intervalId = setInterval(tryToCallTheFunction, 300)

  function tryToCallTheFunction() {
    limit++

    if (hasScriptAvailable || limit >= 50) {
      clearInterval(intervalId)
    } else {
      const targetEelement = $('#schedule-time')
      if (typeof targetEelement.datetimepicker === 'function') {
        hasScriptAvailable = true
        const today = new Date()
        targetEelement.datetimepicker({
          theme:'light',
          format:'Y-m-d H:i:s',
          formatDate:'Y-m-d H:i:s',
          minDate: today
        })
      }
    }
  }
}

const callColorPickerWhenItIsAvailable = () => {
  let limit = 0
  let hasScriptAvailable = false

  const intervalId = setInterval(tryToCallTheFunction, 300)
  
  function tryToCallTheFunction() {
    limit++

    if (hasScriptAvailable || limit >= 50) {
      clearInterval(intervalId)
    } else {
      const targetEelement = $('.color-picker-rgb')
      if (typeof targetEelement.colorpicker === 'function') {
        hasScriptAvailable = true
        targetEelement.colorpicker({
          format: 'hex',
          popover: {
            placement: 'left'
          },
        })
      }
    }
  }
}