import Rete from 'rete'

import CONST_SOCKET from '../../const/socket'
import CONST_COMPONENT from '../../const/component'
import CONST_LINGUAL_NAME from '../../const/lingualName'
import BroadcastingTemplate from './BroadcastingTemplate'
import { lang } from '../../assets/lang'

export default class BroadcastingComponent extends Rete.Component {
  constructor(config) {
    super(CONST_COMPONENT.BROADCASTING)

    this.data.component = BroadcastingTemplate
    this.socket = config ? config.sockets : null
  }

  builder(node) {
    node.lingualName = CONST_LINGUAL_NAME.BROADCASTING
    this.setConnection()

    const broadcastingInput = new Rete.Input(
      CONST_SOCKET.BROADCASTING_INPUT, 
      lang.broadcasting_socket_input_reply, 
      this.socket.broadcastingInput, 
      false
    )

    const broadcastingOutput = new Rete.Output(
      CONST_SOCKET.BROADCASTING_OUTPUT, 
      lang.broadcasting_socket_output_next, 
      this.socket.broadcastingOutput, 
      false
    )
    
    return node
      .addInput(broadcastingInput)
      .addOutput(broadcastingOutput)
  }

  // eslint-disable-next-line
  worker(node, inputs, outputs) {}

  setConnection() {
    this.socket.broadcastingInput.combineWith(this.socket.newPostbackOutputBroadcasting)

    this.socket.broadcastingOutput.combineWith(this.socket.textInput)
    this.socket.broadcastingOutput.combineWith(this.socket.imageInput)
    this.socket.broadcastingOutput.combineWith(this.socket.videoInput)
    this.socket.broadcastingOutput.combineWith(this.socket.audioInput)
    this.socket.broadcastingOutput.combineWith(this.socket.fileInput)
  }
}