import { nanoid } from 'nanoid/async'
import { broadcastingModal } from './broadcastingModal'
import { updateNodeConnections } from '../../utils/functions'

// eslint-disable-next-line
export default async (node, editor, defaultData) => {
  const oldData = node.data
  const { value: broadcastingData } = await broadcastingModal(oldData, node)

  if (broadcastingData && Object.values(broadcastingData).length > 0) {
    // Createbroadcasting unique ID if there is not any
    if (! ('uniqueId' in node.data)) {
      node.data.uniqueId = await nanoid(15)
    }
    Object.assign(node.data, broadcastingData)
    node.update()
    updateNodeConnections(node)
  }

  return false
}