import { App } from '../../App.js'
import CONST_COMPONENT from '../../const/component'

// eslint-disable-next-line
export default async (component, event, ui) => {
  App.editor.view.area.pointermove(event)
  const { x, y } = App.editor.view.area.mouse

  // Ignore creation of the default node
  // const broadcactingNode = await component.createNode()
  // broadcactingNode.position = [x, y]

  const actionButtonComponent = App.components.get(CONST_COMPONENT.ACTION_BUTTON)
  const actionButtonInstance = new actionButtonComponent(App.config)
  const actionButtonNode = await actionButtonInstance.createNode()
  actionButtonNode.position = [x, y]

  App.editor.addNode(actionButtonNode)

  const defaultData = {
    selectedActionButton: 'broadcasting',
  }
  // Handle node's data and modal
  await App.handleNodeData(actionButtonNode, App.editor, defaultData)
}