import Swal from 'sweetalert2'

import SocketName from '../../const/socket'
import ComponentName from '../../const/component'
import { hasInputConnectionWith } from '../../utils/functions'

const newPostbackOutputSequenceClass = SocketName.NEW_POSTBACK_OUTPUT_SEQUENCE.toLowerCase()

export default {
  install(editor, options) { // eslint-disable-line
    editor.on('connectioncreated', (connection) => {
      const nodeInput = connection.input.node
      const nodeOutput = connection.output.node

      if (
        (ComponentName.SEQUENCE_SINGLE === nodeOutput.name) 
        && (ComponentName.POSTBACK_NEW === nodeInput.name)
      ) {
        if (Array.isArray(nodeInput.vueContext.$children)) {
          for (const child of nodeInput.vueContext.$children) {
            if (child.$el.classList.contains(newPostbackOutputSequenceClass)) {
              $(child.$el).parent().hide()

            }
          }
        }

        if (Swal.isVisible()) {  
          const nodeClass = '.node-id-' + nodeInput.id
          $(nodeClass).find('.new-postback-hidable-field').parent().hide()
        }
      }

      toggleNextSocketWhenNewPostbackConnectedWithSequenceSingle('none', nodeInput, nodeOutput)
    })

    editor.on('connectionremoved', (connection) => {
      const nodeInput = connection.input.node
      const nodeOutput = connection.output.node

      if (
        (ComponentName.SEQUENCE_SINGLE === nodeOutput.name) 
        && (ComponentName.POSTBACK_NEW === nodeInput.name)
      ) {
        if (Array.isArray(nodeInput.vueContext.$children)) {
          for (const child of nodeInput.vueContext.$children) {
            if (child.$el.classList.contains(newPostbackOutputSequenceClass)) {
              $(child.$el).parent().show()
            }
          }
        }

        if (Swal.isVisible()) {  
          const nodeClass = '.node-id-' + nodeInput.id
          $(nodeClass).find('.new-postback-hidable-field').parent().show()
        }        
      }

      toggleNextSocketWhenNewPostbackConnectedWithSequenceSingle('block', nodeInput, nodeOutput)
    })
  }
}

const toggleNextSocketWhenNewPostbackConnectedWithSequenceSingle = (hideOrShow, nodeInput, nodeOutput) => {
  if (ComponentName.POSTBACK_NEW === nodeOutput.name) {
    const hasInputConneciton = hasInputConnectionWith(nodeOutput, SocketName.NEW_POSTBACK_INPUT, SocketName.SEQUENCE_SINGLE_OUTPUT)
    if (hasInputConneciton) {
      if (nodeInput.vueContext.$children[0] && nodeInput.vueContext.$children[0].$el) {
        nodeInput.vueContext.$children[0].$el.parentNode.style.display = hideOrShow
      }
    }
  }
}