import { lang } from '../../assets/lang'
import { timezones } from '../../data'
import { getSelectOptionsFromArrayOfObjects } from '../../utils/functions'

export const broadcastingHtml = 
'<div class="text-left mt-4">' +
  '<div class="form-group pb-3">' +
    '<label>' + lang.broadcasting_html_campaign_name + '</label>' +
    '<input type="text" name="campaign-name" id="campaign-name" class="form-control">' +                     
  '</div>' +

  '<div class="form-group pb-3">' +
    '<label class="pb-1">' + lang.broadcasting_html_include_label_ids + '</label>' +
    '<select id="include-label-ids" class="form-control select2" multiple="">' +
    '</select>' +
  '</div>' +

  '<div class="form-group pb-3">' +
    '<label class="pb-1">' + lang.broadcasting_html_exclude_label_ids + '</label>' +
    '<select id="exclude-label-ids" class="form-control select2" multiple=""></select>' +
  '</div>' +

  '<div class="form-group pb-3">' +
    '<label class="pb-1">' + lang.broadcasting_html_sending_time + '</label>' +
    '<br />' +
    '<label class="custom-switch mt-2">' +
      '<input type="checkbox" name="sending-time" id="sending-time" class="custom-switch-input">' +
      '<span class="custom-switch-indicator"></span>' +
      '<span class="custom-switch-description">' + lang.broadcasting_html_send_later + '</span>' +
    '</label>' +
  '</div>' +

  '<div class="schedule-time-group d-none" id="schedule-time-group">' +
    '<div class="form-group pb-3">' +
      '<label>' + lang.broadcasting_html_schedule_time + '</label>' +
      '<input type="text" name="schedule-time" id="schedule-time" class="form-control">' +                     
    '</div>' +

    '<div class="form-group pb-3">' +
      '<label class="pb-1">' + lang.broadcasting_html_timezone + '</label>' +
      '<select id="timezone" class="form-control select2">' + 
      getSelectOptionsFromArrayOfObjects(timezones) +
      '</select>' +
    '</div>' +
  '</div>' +  
'</div>' 
