import Swal from 'sweetalert2'
import { lang } from '../../assets/lang'
import { broadcastingHtml } from './broadcastingHtml'

import {
  makeAjaxCall,
  sweetAlertCommonParams,
  selectedOptionsTextArray,
  getSelectOptionsFromArrayOfObjects,
} from '../../utils/functions'

let isFilesAddedToHead = false
var telegram_bot_id = window.xitFlowBuilderData.telegram_bot_id

// eslint-disable-next-line
export const broadcastingModal = async (oldData, node) => {
  const commonParams = sweetAlertCommonParams(node, '22rem')
  
  return await Swal.fire({
    ...commonParams,
    title: lang.broadcasting_modal_title,
    html: broadcastingHtml,

    willOpen: () => {
      $('.select2').select2({ 
        width: '100%',
        dropdownParent: $('#swal2-content')
      })

      if (false === isFilesAddedToHead) {
        /** Loads lazily bootstrap-colorpicker script */
        $('head').append('<script src="https://cdn.jsdelivr.net/npm/bootstrap-colorpicker@3.4.0/dist/js/bootstrap-colorpicker.min.js"></script>')
        $('head').append('<link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/bootstrap-colorpicker@3.4.0/dist/css/bootstrap-colorpicker.min.css">')

        $('head').append('<script src="https://cdnjs.cloudflare.com/ajax/libs/jquery-datetimepicker/2.4.3/jquery.datetimepicker.min.js"></script>')
        $('head').append('<link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/jquery-datetimepicker/2.4.3/jquery.datetimepicker.min.css">')

        isFilesAddedToHead = true
      }

      const campaignNameElement = $('#campaign-name')
      const includeLabelIdsElement = $('#include-label-ids')
      const excludeLabelIdsElement = $('#exclude-label-ids')

      const sendingTimeEelement = $('#sending-time')
      const scheduleTimeGroupElement = $('#schedule-time-group')

      const scheduleTimeElement = $('#schedule-time')
      const timezoneElement = $('#timezone')

      const labelIdsData = {
        url: window.xitFlowBuilderData.routes.flow_builder_get_label_dropdown,
        data: { 
          telegram_bot_id,
        }
      }

      // Load label ids
      makeAjaxCall(function(response) {
        if (Array.isArray(response)) {
          const labelOptions = getSelectOptionsFromArrayOfObjects(response)
          includeLabelIdsElement.html(labelOptions)
          excludeLabelIdsElement.html(labelOptions)

          includeLabelIdsElement.val(oldData.telegram_bot_label_ids || '').select2().trigger('change')
          excludeLabelIdsElement.val(oldData.excluded_telegram_bot_label_ids || '').trigger('change')    
        }
      }, labelIdsData)

      // eslint-disable-next-line
      function toggleSendLaterOptions(isSendingTimeChecked, oldData) {
        if (isSendingTimeChecked) {
          scheduleTimeGroupElement.removeClass('d-none').addClass('d-block')
        } else {
          scheduleTimeGroupElement.removeClass('d-block').addClass('d-none')
        }
      }

      sendingTimeEelement.prop('checked', ('later' === oldData.schedule_type) ? true : false)
      
      const isSendingTimeChecked = sendingTimeEelement.prop('checked')
      toggleSendLaterOptions(isSendingTimeChecked, oldData)
      
      sendingTimeEelement.on('change', function() {
        toggleSendLaterOptions(this.checked, oldData)
      })

      setTimeout(function() {
        const today = new Date()
        $('#schedule-time').datetimepicker({
          theme:'light',
          format:'Y-m-d H:i:s',
          formatDate:'Y-m-d H:i:s',
          minDate: today
        })
      }, 1500);

      // Populate old values
      campaignNameElement.val(oldData.campaign_name || '')
      includeLabelIdsElement.val(oldData.telegram_bot_label_ids || '').select2().trigger('change')
      excludeLabelIdsElement.val(oldData.excluded_telegram_bot_label_ids || '').trigger('change')

      scheduleTimeElement.val(oldData.schedule_time || '')
      timezoneElement.val(oldData.timezone || '').trigger('change')

      $('[data-toggle="tooltip"]').tooltip()
    },

    preConfirm: () => {
      const campaign_name = $('#campaign-name').val()

      const telegram_bot_label_ids = $('#include-label-ids').val()
      const includeLabelIdsText = $('#include-label-ids option:selected')
      const telegram_bot_label_ids_names = selectedOptionsTextArray(includeLabelIdsText)

      const excluded_telegram_bot_label_ids = $('#exclude-label-ids').val()
      const excludeLabelIdsText = $('#exclude-label-ids option:selected')
      const excluded_telegram_bot_label_ids_names = selectedOptionsTextArray(excludeLabelIdsText)

      const schedule_type = $('#sending-time').prop('checked')
      const schedule_time = $('#schedule-time').val()
      const	timezone = $('#timezone').val()

      if (! campaign_name) {
        Swal.showValidationMessage(lang.broadcasting_msg_campaign_name)
        return
      }

      if (schedule_type) {
        if (! schedule_time) {
          Swal.showValidationMessage(lang.broadcasting_msg_schedule_time)
          return
        }

        if (! timezone) {
          Swal.showValidationMessage(lang.broadcasting_msg_timezone)
          return
        }
      }

      return {
        campaign_name,

        telegram_bot_label_ids,
        telegram_bot_label_ids_names,

        excluded_telegram_bot_label_ids,
        excluded_telegram_bot_label_ids_names,

        schedule_type: (true === schedule_type) ? 'later' : 'now',
        schedule_time,
        timezone,
      }
    },    
  })
}
