<template>
  <div class="xit-action-buttons btn-toolbar" role="toolbar" aria-label="Toolbar with button groups">
    
    <div v-if="hasDataSaved" class="btn-group mr-2" role="group" aria-label="Get Embeded Code">
      <button 
        type="button"
        class="btn btn-outline-primary"
        @click.stop="getEmbededCode" data-toggle="tooltip" :data-original-title="embeded_code"
      >
        <i class="fas fa-code"></i>
      </button>
    </div>

    <div class="dropdown d-inline mr-2">
      <button @click="handleSelect2" class="btn btn-outline-primary dropdown-toggle" type="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
        {{ test_button }}
      </button>
      <div id="dropdown-select2-parent" class="dropdown-menu pl-2 pr-2" onclick="event.stopPropagation()">
        <div class="form-group mb-3">
          <label>{{ test_button_label }}</label>
          <select id="subscribers" class="form-control select2" multiple></select>
        </div>
        <div class="form-group mb-2">
          <button 
            @click.stop="handleTest"
            id="bot-send-button" 
            class="btn btn-primary"
            disabled
          >
            <i class="fas fa-paper-plane"></i>  {{ send_button }}
          </button>
        </div>
      </div>
    </div>

    <div class="btn-group mr-2" role="group" aria-label="Back">
      <button @click.stop="backToMainMenu" class="btn btn-outline-primary" type="button" data-toggle="tooltip" :data-original-title="back">
        <i class="fas fa-step-backward back_button_icon"></i>
      </button>
    </div>
    
    <div class="btn-group mr-2" role="group" aria-label="Auto Arrage">
      <button 
        type="button"
        class="btn btn-outline-primary"
        @click.stop="handleAutoArrangeOfNodes" data-toggle="tooltip" :data-original-title="rearrange"
      >
        <i class="fa fa-compress-arrows-alt rearrange_button_icon"></i>
      </button>
    </div>

    <div class="btn-group mr-2" role="group" aria-label="Save">
      <button 
        type="button"
        class="action-button-save btn btn-outline-primary"
        @click.stop="handleSave" data-toggle="tooltip" :data-original-title="save"
      >
        <i class="fas fa-save save_button_icon"></i>
      </button>
    </div>

    <div class="btn-group" role="group" aria-label="page-nfo">
      <a 
        :href="media_url"
        target="_blank"
        class="btn btn-outline-primary"
      >
        <span>{{ telegram_username }}</span>
      </a>
    </div>
  </div>
</template>

<script>
import Toast from 'izitoast'
import Swal from 'sweetalert2'
import { App } from '../../App'
import { 
  showError,
  showWarning,
  makeAjaxCall, 
  showAjaxError,
  checksNodesAreConnected,
  addClassIfNodesHaveNoValue,
  addClassIfOutputHasNoConnection,
} from '../../utils/functions'

import { lang } from '../../assets/lang'
import CONST_SOCKET from '../../const/socket'
import CONST_COMPONENT from '../../const/component'

var telegram_bot_id = window.xitFlowBuilderData.telegram_bot_id
var instagram_bot_addon = window.xitFlowBuilderData.instagram_addon

// const  = window.xitFlowBuilderData.base_url
const builder_table_id = window.xitFlowBuilderData.builder_table_id

export default {
  data: () => ({
    test_button: lang.others_buttons_test_button_name,
    test_button_label: lang.others_buttons_test_label_name,
    send_button: lang.others_buttons_test_send_button_name,
    back: lang.others_buttons_title_back,
    save: lang.others_buttons_title_save,
    embeded_code: lang.others_buttons_emebeded_code,
    rearrange: lang.others_buttons_title_rearrange,
    telegram_username: window.xitFlowBuilderData.telegram_username,
    media_url: 'https://t.me/' + window.xitFlowBuilderData.telegram_username,
    hasDataSaved: !! window.xitFlowBuilderData.data,
    visual_flow_campaign_id: builder_table_id > 0 ? builder_table_id : null
  }),

  methods: {
    handleSave(event) { // eslint-disable-line
      
      const that = this

      try {
        this.handleErrors()
      } catch(e) {
        return showWarning(e.message)
      }

      let data = App.editor.toJSON()
      data = JSON.stringify(data)
      
      const saveButtonElement = $('.xit-action-buttons .action-button-save')

      saveButtonElement.removeClass('btn-outline-primary')
      saveButtonElement.addClass('btn-primary', 'disabled', 'btn-progress')

      $.ajax({
        method: 'POST',
        dataType: 'JSON',
        url: window.xitFlowBuilderData.routes.flow_builder_submit,
        data: {telegram_bot_id,builder_table_id,instagram_bot_addon,flow_data:data},
        beforeSend: function (xhr) {
            xhr.setRequestHeader('X-CSRF-TOKEN', window.xitFlowBuilderData.csrf_token);
        },
        success: function(response) {
          saveButtonElement.removeClass('btn-primary', 'disabled', 'btn-progress')
          saveButtonElement.addClass('btn-outline-primary') 

          if(response.status == '0') {
            showError(response.message)
          } else {
            that.hasDataSaved = true
            that.visual_flow_campaign_id = response.visual_flow_campaign_id
            window.hasXitFlowBuilderData = true

            Toast.success({
              title: lang.utils_title_success,
              message: response.message,
              position: 'bottomRight' 
            })
          }

          return false
        },

        // eslint-disable-next-line
        error:function(xhr, status) {
          saveButtonElement.removeClass('btn-primary', 'disabled', 'btn-progress')
          saveButtonElement.addClass('btn-outline-primary') 
          showAjaxError(xhr)

          return false
        }
      })

      return false
    },

    handleTest(event) { // eslint-disable-line

      const subscribers = $('#subscribers').val()
    
      try {
        
        if (subscribers.length < 1) {
          throw new Error(lang.others_buttons_msg_test_button_subscriber_required)
        }

        this.handleErrors()

      } catch(e) {
        return showWarning(e.message)
      }

      let data = App.editor.toJSON()
      data = JSON.stringify(data)
      
      const botSendButtonElement = $('#bot-send-button')
      botSendButtonElement.removeClass('btn-primary')
      botSendButtonElement.addClass('btn-progress')

      $.ajax({
        method: 'POST',
        dataType: 'JSON',
        url: window.xitFlowBuilderData.routes.flow_builder_submit_test,
        data: {
          telegram_bot_id,
          builder_table_id,
          instagram_bot_addon,
          flow_data: data,
          subscribers,
        },
        beforeSend: function (xhr) {
            xhr.setRequestHeader('X-CSRF-TOKEN', window.xitFlowBuilderData.csrf_token);
        },
        success: function(response) {
          botSendButtonElement.removeClass('btn-progress')
          botSendButtonElement.addClass('btn-primary') 

          if(response.status == '0') {
            showError(response.message)
          } else {
            window.hasXitFlowBuilderData = true

            Toast.success({
              title: lang.utils_title_success,
              message: response.message,
              position: 'bottomRight' 
            })
          }

          return false
        },

        // eslint-disable-next-line
        error:function(xhr, status) {
          botSendButtonElement.removeClass('btn-progress')
          botSendButtonElement.addClass('btn-primary')
          showAjaxError(xhr)

          return false
        }
      })

      return false
    },

    handleAutoArrangeOfNodes (event) {
      event.preventDefault()

      const node = App.getDefaultComponent()
      node.position = [-450, -50]

      App.editor.trigger('arrange', { node })

      return false
    },

    backToMainMenu () {
      const go_back_link = window.xitFlowBuilderData.go_back_link
      window.location.replace(go_back_link);
    },

    handleErrors() {

      const nodeCount = App.editor.nodes.length
      
      if (1 === nodeCount) {
        throw new Error(lang.others_buttons_msg_components_required)
      }

      if (checksNodesAreConnected('node--has-no-connection')) {
        throw new Error(lang.others_buttons_msg_components_components_connection_required)
      }

      if (addClassIfNodesHaveNoValue('node--has-no-value')) {
        throw new Error(lang.others_buttons_msg_components_components_data_required)
      }

      if (addClassIfOutputHasNoConnection(CONST_COMPONENT.POSTBACK_NEW, CONST_SOCKET.NEW_POSTBACK_OUTPUT, 'node--has-no-value')) {
        throw new Error(lang.others_buttons_msg_components_new_postback_next_connection_required)
      }

      if (addClassIfOutputHasNoConnection(CONST_COMPONENT.USER_INPUT_FLOW_SINGLE, CONST_SOCKET.USER_INPUT_FLOW_SINGLE_OUTPUT, 'node--has-no-value')) {
        throw new Error(lang.plugin_incompatible_connection_user_input_flow_builder)
      }
    },

    handleSelect2() {
      $('.select2').select2({ 
        width: '100%',
        dropdownParent: $('#dropdown-select2-parent'),
        minimumInputLength: 1,
        ajax: {
          cache: true,
          delay: 250,
          dataType: 'json',
          url: function (params) {
            return window.xitFlowBuilderData.routes.get_bot_subscribers
              .replace(':botId', telegram_bot_id)
              .replace(':key', params.term)
          },
          data: function (params) { // eslint-disable-line
            return null
          },
          processResults: function (data, params) { // eslint-disable-line
            return {
              results: data.map(
                (val, key) => ({ id: val.subscriber_id, text: `${val.first_name} ${val.last_name}` }) // eslint-disable-line
              )
            }
          },
        },
      })
    },

    getEmbededCode() {
      const getEmbededData = {
        url: window.xitFlowBuilderData.routes.get_embeded_code,
        data: { 
          visual_flow_campaign_id: this.visual_flow_campaign_id,
        }
      }

      makeAjaxCall(function(response) {
        Swal.fire({
          width: '800px',
          html: '' +
            '<div class="container text-left">' +
              '<div class="row">' +
                '<div class="col-12 col-md-9">' +
                  '<h6>JavaScript Embed Code</h6>' +
                  '<textarea class="form-control">' + response.str1 + '</textarea>' +
                  '<br>' +
                  '<h6>t.me Short-link</h6>' +
                  '<input type="text" class="form-control" value="' + response.str2 + '">' +
                  '<br>' +
                '</div>' +

                '<div class="col-12 col-md-3">' +
                  '<h6>QR Code</h6>' +
                  '<a target="_BLANK" href="">' +
                    '<img id="put_qr_code" class="img-fluid img-thumbnail" src="'+ response.str3 +'">' +
                  '</a>' +
                '</div>' +
              '</div>' +
            '</div>',
          showCloseButton: true,
          showConfirmButton: false,
        })
      }, getEmbededData)
    },
  },

  mounted() {
    
    $('[data-toggle="tooltip"]').tooltip()

    const that = this
    const botSendButtonElement = $('#bot-send-button')

    $(document).on('keydown', function(event) {
      if(event.ctrlKey && (event.which === 83)) {
        event.preventDefault()

        that.handleSave(event)
      }

      return false
    })

    $('#subscribers').on('change', function() {
      const selectedVal = $(this).val()
      if (selectedVal.length > 0) {
        botSendButtonElement.attr('disabled', false)
      } else {
        botSendButtonElement.attr('disabled', true)
      }
    })
  },

  beforeDestroy() { // eslint-disable-line
    const tooltip = $('[data-toggle="tooltip"]')
    
    if (tooltip) {
      $('[data-toggle="tooltip"]').tooltip('dispose')
    }
  },
}
</script>

<style>
.xit-action-buttons {
  position: absolute;
  right: 0;
  background-color: #fff !important;
}
#dropdown-select2-parent.dropdown-menu {
  width: 320px;
}
</style>