<template>
  <div 
    class="dockmenu-content-elements"
    :style="{ height: elementContentHeight + 'px' }"
  >
    <div 
      id="content-elements-scrollbar"
      class="content-elements-scrollbar"
    >
      <div class="content-elements-elements justify-content-center">
        <type
          :key="type.component"
          :icon="type.icon"
          :type="type.component"
          :title="type.name"
          v-for="type in types"
          :visible="type.visibility"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Type from './Type'
import lingualName from '../../const/lingualName'
import CONST_COMPONENT from '../../const/component'
var sequence_addon = window.xitFlowBuilderData.sequence_addon
// var instagram_addon = window.xitFlowBuilderData.instagram_addon
var user_input_flow_addon = window.xitFlowBuilderData.user_input_flow_addon
var messenger_bot_condition = window.xitFlowBuilderData.messenger_bot_condition
var ecommerce_module_existence = window.xitFlowBuilderData.ecommerce_module_existence

export default {
  name: 'Elements',
  components: {
    Type,
  },
  data: () => ({
    elementContentHeight: null,
    types: [
      {
        icon: 'fas fa-paragraph text',
        name: lingualName.TEXT,
        component: CONST_COMPONENT.TEXT,
        visibility: 1,
      },

      {
        icon: 'far fa-image image',
        name: lingualName.IMAGE,
        component: CONST_COMPONENT.IMAGE,
        visibility: 1,
      },

      {
        icon: 'fas fa-microphone-alt audio',
        name: lingualName.AUDIO,
        component: CONST_COMPONENT.AUDIO,
        visibility: 1,
      },       
       
      {
        icon: 'fab fa-youtube video',
        name: lingualName.VIDEO,
        component: CONST_COMPONENT.VIDEO,
        visibility: 1,
      },      
             
      {
        icon: 'fas fa-paperclip file',
        name: lingualName.FILE,
        component: CONST_COMPONENT.FILE,
        visibility: 1,
      },

      {
        icon: 'fas fa-comment-dots quick-reply',
        name: lingualName.QUICK_REPLY,
        component: CONST_COMPONENT.QUICK_REPLY,
        visibility: 0,
      },

      {
        icon: 'fas fa-store-alt ecommerce',
        name: lingualName.ECOMMERCE,
        component: CONST_COMPONENT.ECOMMERCE,
        visibility: ecommerce_module_existence,
      },

      {
        icon: 'fas fa-greater-than-equal conditional-reply',
        name: lingualName.CONDITION,
        component: CONST_COMPONENT.CONDITION,
        visibility: messenger_bot_condition ? 1 : 0,
      },

      {
        icon: 'fas fa-tint drip',
        name: lingualName.SEQUENCE,
        component: CONST_COMPONENT.SEQUENCE,
        visibility: sequence_addon,
      },

      {
        icon: 'fab fa-stack-overflow input-flow',
        name: lingualName.USER_INPUT_FLOW,
        component: CONST_COMPONENT.USER_INPUT_FLOW,
        visibility: user_input_flow_addon,
      },

      {
        icon: 'fas fa-rocket trigger-keyword',
        name: lingualName.TRIGGER,
        component: CONST_COMPONENT.TRIGGER,
        visibility: 1,
      },

      {
        icon: 'fas fa-paper-plane postback-new',
        name: lingualName.POSTBACK_NEW,
        component: CONST_COMPONENT.POSTBACK_NEW,
        visibility: 1,
      }, 
    ]
  }),

  methods: {
    handleScrollbar() {
      $('#content-elements-scrollbar').niceScroll({
        cursorcolor: '#6e7fff'
      })
    },

    handleElementContentHeight () {
      const offsetHeight = document.getElementById('xit-flow-builder').offsetHeight
      if (parseInt(offsetHeight, 10) <= 40) {
        this.elementContentHeight = 0
      } else {
        this.elementContentHeight = document.getElementById('xit-flow-builder').offsetHeight - 100
      }
    },    
  },

  mounted() {
    this.handleScrollbar()
    this.handleElementContentHeight()

    this.$nextTick(function() {
      window.addEventListener('resize', this.handleScrollbar)
      window.addEventListener('resize', this.handleElementContentHeight)
    })    
  },

  beforeDestroy() { // eslint-disable-line
    const contentElementsScrollbarElement = $('#content-elements-scrollbar')
    if (contentElementsScrollbarElement) {
      contentElementsScrollbarElement.getNiceScroll().remove()
    }
    
    window.removeEventListener('resize', this.handleScrollbar)
    window.removeEventListener('resize', this.handleElementContentHeight)   
  }  
}
</script>

<style scoped>
/* Important for nicescroll */
.content-elements-scrollbar {
  height: 100% !important;
}
.content-elements-elements {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 10px 6px;
}
</style>
