<template>
  <div 
    class="xit-fb-template node" 
    @dblclick.stop="handleClick(node)"
    :class="[camelCase(node.name), hasData(node), isSelected(node)]" 
  >
    <div class="title">
      <p class="title-content"><i class="far fa-hand-pointer card-audio-icon"></i> {{node.lingualName}}</p>
    </div>

    <div class="card-body-handler card_data_body p-0">
      <div class="card-data list-small-padde">
        <p 
          class="text-center mb-2 postback_title"
          v-if="node.data.actionType && node.data.actionTypeText"
        >{{ node.data.actionTypeText }}</p>

        <div v-if="node.data.actionType && 'messenger_engagement_plugin' === node.data.actionType">
          
          <span class="list-group-item list-group-item-action flex-column align-items-start" v-if="'send_to_messenger' === node.data.pluginType && node.data.ctaButtonTypeText">
            <div class="d-flex w-100 justify-content-between">
              <small class="text-muted">{{ cta_button_text }}</small>
              <h6 class="mb-1 card-data-title">{{ node.data.ctaButtonTypeText}} </h6>              
            </div>           
          </span>
          <div class="text_message mb-2" v-if="node.data.chatboxWelcomeMessage">
            {{node.data.chatboxWelcomeMessage}}
          </div>
          <span class="list-group-item list-group-item-action flex-column align-items-start" v-if="node.data.chatboxDisplayName">
            <div class="d-flex w-100 justify-content-between">
              <small class="text-muted">{{ action_button_html_chatbox_display_name }}</small>
              <h6 class="mb-1 card-data-title">{{ startCase(node.data.chatboxDisplayName) }} </h6>              
            </div>           
          </span>
          <span class="list-group-item list-group-item-action flex-column align-items-start" v-if="node.data.chatboxPosition">
            <div class="d-flex w-100 justify-content-between">
              <small class="text-muted">{{ action_button_html_chatbox_position }}</small>
              <h6 class="mb-1 card-data-title">{{ startCase(node.data.chatboxPosition) }} </h6>              
            </div>           
          </span>
          <span class="list-group-item list-group-item-action flex-column align-items-start" v-if="node.data.buttonText">
            <div class="d-flex w-100 justify-content-between">
              <small class="text-muted">{{ button_text }}</small>
              <h6 class="mb-1 card-data-title">{{ startCase(node.data.buttonText) }} </h6>              
            </div>           
          </span>          
          <span class="list-group-item list-group-item-action flex-column align-items-start" v-if="node.data.reference">
            <div class="d-flex w-100 justify-content-between">
              <small class="text-muted">{{ reference }}</small>
              <h6 class="mb-1 card-data-title">{{ node.data.reference}} </h6>              
            </div>           
          </span>
          <span class="list-group-item list-group-item-action flex-column align-items-start" v-if="node.data.buttonBgColor">
            <div class="d-flex w-100 justify-content-between">
              <small class="text-muted">{{ button_background_color }}</small>
              <h6 class="mb-1 card-data-title"><span class="border py-0 px-2" :style="'background:'+node.data.buttonBgColor"></span></h6>              
            </div>           
          </span>
          <span class="list-group-item list-group-item-action flex-column align-items-start" v-if="node.data.buttonTextColor">
            <div class="d-flex w-100 justify-content-between">
              <small class="text-muted">{{ button_text_color }}</small>
              <h6 class="mb-1 card-data-title"><span class="border py-0 px-2" :style="'background:'+node.data.buttonTextColor"></span></h6>              
            </div>           
          </span>
          <span class="list-group-item list-group-item-action flex-column align-items-start" v-if="node.data.chatboxBgColor">
            <div class="d-flex w-100 justify-content-between">
              <small class="text-muted">{{ action_button_html_chatbox_header_background }}</small>
              <h6 class="mb-1 card-data-title"><span class="border py-0 px-2" :style="'background:'+node.data.chatboxBgColor"></span></h6>              
            </div>           
          </span>
          <span class="list-group-item list-group-item-action flex-column align-items-start" v-if="node.data.chatboxTextColor">
            <div class="d-flex w-100 justify-content-between">
              <small class="text-muted">{{ action_button_html_chatbox_header_color }}</small>
              <h6 class="mb-1 card-data-title"> <span class="border py-0 px-2" :style="'background:'+node.data.chatboxTextColor"></span></h6>              
            </div>           
          </span>
          <span class="list-group-item list-group-item-action flex-column align-items-start" v-if="node.data.labelIdTextsArray && node.data.labelIdTextsArray.length > 0">
            <div class="d-flex w-100 justify-content-between">
              <small class="text-muted">{{ labels }}</small>
              <h6 class="mb-1 card-data-title">{{ node.data.labelIdTextsArray.join(', ') }} </h6>              
            </div>           
          </span>
        </div>
      </div>

      <div class="card-sockets">
        <div class="output_content">
          <div class="output text-right" v-for="output in outputs()" :key="output.key" :class="hasConnection(output) ? 'has-connection' : ''">
            <div class="output-title">{{output.name}}</div>
            <Socket v-socket:output="output" type="output" :socket="output.socket"></Socket>
          </div>
        </div>
        <div class="input_content">
          <div class="input text-left" v-for="input in inputs()" :key="input.key" :class="hasConnection(input) ? 'has-connection' : ''">
            <Socket v-socket:input="input" type="input" :socket="input.socket"></Socket>
            <div class="input-title">{{input.name}}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import VueRenderPlugin from 'rete-vue-render-plugin'

import { lang } from '../../assets/lang'
import { templateComponentMethods } from '../../utils/functions'

export default {
  name: 'AudioTemplate',
  mixins: [VueRenderPlugin.mixin],
  components: {
    Socket: VueRenderPlugin.Socket
  },

  data: () => ({
    baseUrl: window.xitFlowBuilderData.base_url,

    action_button_html_chatbox_header_background: lang.action_button_html_chatbox_header_background,
    action_button_html_chatbox_header_color: lang.action_button_html_chatbox_header_color,
    action_button_html_chatbox_display_name: lang.action_button_html_chatbox_display_name,
    action_button_html_chatbox_welcome_message: lang.action_button_html_chatbox_welcome_message,
    action_button_html_chatbox_position: lang.action_button_html_chatbox_position,
    action_button_html_chatbox_position_leftTop: lang.action_button_html_chatbox_position_leftTop,
    action_button_html_chatbox_position_leftBottom: lang.action_button_html_chatbox_position_leftBottom,
    action_button_html_chatbox_position_rightTop: lang.action_button_html_chatbox_position_rightTop,
    action_button_html_chatbox_position_rightBottom: lang.action_button_html_chatbox_position_rightBottom,
    action_button_html_chatbox_position_tooltip: lang.action_button_html_chatbox_position_tooltip,
    action_button_html_chatbox_position_offset_x: lang.action_button_html_chatbox_position_offset_x,
    action_button_html_chatbox_position_offset_y: lang.action_button_html_chatbox_position_offset_y,
    action_button_html_chatbox_position_offset_x_tooltip: lang.action_button_html_chatbox_position_offset_x_tooltip,
    action_button_html_chatbox_position_offset_y_tooltip: lang.action_button_html_chatbox_position_offset_y_tooltip,
    action_button_html_chatbox_init_delay: lang.action_button_html_chatbox_init_delay,

    button_text: lang.action_button_html_button_text,
    button_background_color: lang.action_button_html_button_background,
    button_text_color: lang.action_button_html_button_text_color,
    button_hover_background_color: lang.action_button_html_button_hover_background,
    button_text_hover_color: lang.action_button_html_button_text_hover_color,
    button_size: lang.action_button_html_button_size,
    
    reference: lang.action_button_html_reference,
    labels: lang.action_button_html_select_labels,
  }),

  methods: {
    ...templateComponentMethods,
  }
}
</script>

<style scoped>
.card-data-media-audio {border-radius: 7px;}
.input_content { width:50% !important;margin-top: 25px;}
.output_content{ width:50% !important;float:right !important;}
.card-sockets { padding: 5px 0 !important;}
.card-data-media { font-size: 14px; }

</style>